@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("..//fonts/oswaldregular.woff2") format("woff2"), url("..//fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("..//fonts/oswaldmedium.woff2") format("woff2"), url("..//fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
.align-center {
  margin: 0 auto;
}
.align-left {
  margin-left: auto;
  margin-right: 0;
}
.align-right {
  margin-right: auto;
  margin-left: 0;
}
.arial-400 {
  font-family: "Arial", sans-serif;
  font-weight: 400;
}
.oswald-400 {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
}
.oswald-500 {
  font-family: "Oswald", sans-serif;
  font-weight: 500;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  margin: 0 auto;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  color: #ffffff;
  font-size: 14px;
  line-height: 14px;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  padding: 12px 5px;
  background-color: #EE9543;
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: #eb8323;
}
.button:active {
  color: #ffffff99;
}
.button:disabled {
  pointer-events: none;
  opacity: 0.6;
}
.custom-marker {
  content: "";
  position: absolute;
  left: -34px;
  width: 10px;
  height: 10px;
  background-color: #EE9543;
}
.page {
  height: 100%;
  word-wrap: break-word;
}
.page__body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page__body {
    font-size: 16px;
    line-height: 24px;
  }
}
.main-header__container {
  margin: 0 auto;
  width: 280px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding-top: 16px;
  padding-bottom: 16px;
}
.main-header__link:hover {
  opacity: 0.6;
}
.main-header__link:active {
  opacity: 0.3;
}
.main-header__link,
.main-header__link svg {
  width: 35px;
  height: 34px;
}
.main-header__logo--desktop,
.main-header__logo--tablet {
  display: none;
}
@media (min-width: 768px) {
  .main-header__container {
    width: 708px;
    padding-top: 30px;
    padding-bottom: 30px;
  }
  .main-header__link,
  .main-header__link svg {
    width: 45px;
    height: 45px;
  }
  .main-header__logo--tablet {
    display: block;
  }
  .main-header__logo--mobile,
  .main-header__logo--desktop {
    display: none;
  }
}
@media (min-width: 1440px) {
  .main-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 42px;
    padding-bottom: 34px;
    width: 1220px;
    margin: 0 auto;
  }
  .main-header__container {
    width: auto;
    padding: 0;
    margin: 0;
  }
  .main-header__link,
  .main-header__link svg {
    width: 117px;
    height: 64px;
  }
  .main-header__logo--desktop {
    display: block;
  }
  .main-header__logo--mobile,
  .main-header__logo--tablet {
    display: none;
  }
}
.main-nav__toggle-container {
  width: 280px;
  margin: 0 auto;
  margin-bottom: 25px;
  margin-top: -43px;
  display: flex;
  justify-content: flex-end;
}
.main-nav--closed .main-nav__toggle {
  border: none;
  background: none;
  background-image: url("../img/icon-hamburger.svg");
  background-size: 24px 18px;
  background-position: center;
  background-repeat: no-repeat;
  width: 24px;
  height: 18px;
  cursor: pointer;
  display: block;
}
.main-nav--opened .main-nav__toggle {
  border: none;
  background: none;
  background-image: url("../img/icon-cross.svg");
  background-size: 18px 18px;
  background-position: center;
  background-repeat: no-repeat;
  width: 24px;
  height: 18px;
  cursor: pointer;
  display: block;
}
.main-nav--nojs .main-nav__toggle {
  visibility: hidden;
}
.main-nav--closed .main-nav__list {
  display: none;
}
.main-nav--opened .main-nav__list {
  position: absolute;
  left: 0;
  list-style-type: none;
  padding: 0;
  margin: 0;
  width: 100%;
  min-width: 320px;
  display: flex;
  flex-direction: column;
  z-index: 5;
}
.main-nav--nojs .main-nav__list {
  position: relative;
  list-style-type: none;
  padding: 0;
  margin: 0;
  width: 100%;
  min-width: 320px;
  display: flex;
  flex-direction: column;
}
.main-nav__item {
  text-align: center;
  border-bottom: 1px solid #e6e6e6;
  background-color: #ffffff;
}
.main-nav__item:first-child {
  border-top: 1px solid #e6e6e6;
}
.main-nav__link {
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
  display: inline-block;
  padding-top: 22px;
  padding-bottom: 24px;
  max-width: 280px;
}
.main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__toggle-container {
    width: 708px;
    margin-bottom: 38px;
    margin-top: -68px;
  }
  .main-nav--closed .main-nav__toggle {
    background-size: 44px 27px;
    width: 44px;
    height: 27px;
  }
  .main-nav--opened .main-nav__toggle {
    background-size: 27px 27px;
    width: 44px;
    height: 27px;
  }
}
@media (min-width: 1440px) {
  .main-nav__toggle-container {
    display: none;
  }
  .main-nav--closed .main-nav__list,
  .main-nav--opened .main-nav__list,
  .main-nav--nojs .main-nav__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
    position: relative;
    padding: 0;
    margin: 0;
    width: max-content;
    max-width: 1020px;
    list-style-type: none;
  }
  .main-nav__item:not(:first-child) {
    margin-left: 32px;
  }
  .main-nav__item,
  .main-nav__item:first-child {
    border: none;
    margin-top: 5px;
    margin-bottom: 5px;
  }
  .main-nav__link {
    line-height: 24px;
    padding: 0;
  }
}
